/* Google Font "Raleway" */
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200&display=swap');

/* Affects all elements of html */
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Raleway', sans-serif;
}

/* CSS Varibles for color */
:root {
  --main-color: #56a5eb;
  --main-hover: #3a8ab380;
  --accent-color: #6989b9a1;
  --main-white: #ffffff;
  --accent-white: #f5f5f5;
  --black: #2b2b2b;
  --main-grey: #E5E5E5;
  --background-purple: #F7E9FF;
  --border-grey: #CACDD3;
  --grey-hover: #ddd;

}