h1 {
  text-align: center;
  font-size: 5.4rem;
  margin-bottom: 2rem;
}

h3 {
  text-decoration: underline;
}

h4 {
  margin-top: 1rem;
}

/* UTILITIES */
.previews {
    border-block-color: var(--black);
    border-style: outset;
    width: 50vw;
    margin-bottom: auto;
}

.container {
    width: 100vw;
    height: 100vh;
    align-items: flex-start;
    max-width: 100%;
    margin: 0 auto;
    padding: 2rem;
}

.flexed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* BUTTONS */

.btn {
    font-size: 1.5rem;
    padding: 0 1rem;
    width: 20vw;
    text-align: center;
    border: 0.1rem solid var(--main-color);
    margin: 1rem;
    text-decoration: none;
    color: var(--main-color);
    background-color: var(--accent-white);
}

.btn:hover {
    cursor: pointer;
    box-shadow: 0 0.4rem 1.4rem 0 var(--main-hover);
    transform: translateY(-0.1rem);
    transition: transform 150ms;
}

@media screen and (max-width: 800px) {
  h1 {
    font-size: 3rem;
  }
  .btn {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 2.5rem;
  }
  .btn {
    font-size: 1rem;
  }
}