h3,
h4 {
  margin-bottom: 1rem;
  text-align: center;
}

h3 {
  font-size: 4rem;
  font-weight: 400;
  font-style: normal;
}

h4 {
  font-size: 0.9rem;
  font-style: normal;
  margin-bottom: 2rem;
}

body {
  width: 100vw;
  height: 100vh;
  background-image: url("../../Assets/Background.png");
  color: var(--black);
  max-width: 100%;
}
/* UTILITIES */

.container {
  width: 100vw;
  max-width: 100%;
  padding: 2rem;
}

.profile {
  border-radius: 50%;
  background-image: url("../../Assets/Prof.jpeg");
  background-size: 25rem;
  margin: 0 auto;
  height: 25rem;
  width: 25rem;
}

.flexed {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  margin-bottom: 5rem;
}

.col {
  width: 50%;
}

.resize {
  border-radius: 50%;
  width: 4vw;
  height: 4vw;
  margin: 2rem 2rem;
  float: left;
}

.resize:hover {
  cursor: pointer;
  box-shadow: 0 0.4rem 1.4rem 0 var(--main-hover);
  transform: translateY(-0.1rem);
  transition: transform 150ms;
}

.text {
  display: inline;
}

.row {
  margin: 0 auto;
  width: 5vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
}



@media screen and (max-width: 900px) {
    h3 {
      font-size: 2.5rem;
    }
  
    h4 {
      font-size: 1rem;
    }
    .profile {
      height: 15rem;
      width: 15rem;
      background-size: 15rem;
      margin-bottom: 2rem;
    }
    .flexed {
      flex-direction: column;
      margin-bottom: 2rem;
    }
    .col {
        width: 100%;
    }
    .columns img{
        width: 50%;
        height: auto;
    }
  }
  